import React, {useState, useEffect} from 'react';
import {
  StyleSheet,
  Text,
  View,
  ImageBackground,
} from "react-native";

import {
  Colors,
} from 'react-native/Libraries/NewAppScreen';
import Tts from 'react-native-tts';
import axios from "axios";


const speak = (msg = 'No message given') => {
  Tts.speak(msg)
};

const getRandomInt = (min: number, max: number) => {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min) + min); // The maximum is exclusive and the minimum is inclusive
}

function App() : JSX.Element {
  const num = getRandomInt(1,150);
  const [image, setImage] = useState({uri: `https://raw.githubusercontent.com/PokeAPI/sprites/master/sprites/pokemon/${num}.png`})
  const [name, setName] = useState('');

  const getData = async () => {
    const num = getRandomInt(1, 905);
    const pokemon = await axios.get(`https://pokeapi.co/api/v2/pokemon/${num}/`).then((response) => {
      return response.data
    });
    const name = pokemon?.species?.name;
    const sprite = pokemon?.sprites?.front_default;
    setName(name);
    speak(name);
    setImage({uri: sprite})
  }

  useEffect(() => {getData()},[]);

  return (
    <View style={boxStyles.container}>
      <ImageBackground
        source={image}
        resizeMode="cover"
        style={boxStyles.image}
      ></ImageBackground>
      <Text style={boxStyles.text} onPress={() => getData()}>
        {name}
      </Text>
    </View>
  );
}

const boxStyles = StyleSheet.create({
  container: {
    flex: 1,
  },
  image: {
    flex: 1,
    justifyContent: 'center',
  },
  text: {
    color: 'white',
    fontSize: 42,
    lineHeight: 84,
    fontWeight: 'bold',
    textAlign: 'center',
    backgroundColor: '#000000c0',
    bottom: 0,
  },
});



export default App;